import React from 'react';
import PropTypes from 'prop-types';
import {makeStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
    imageHeader: {
        position: 'relative',
        backgroundColor: theme.palette.grey[800],
        color: theme.palette.common.white,
        marginBottom: theme.spacing(4),
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
    },
    overlay: {
        position: 'absolute',
        top: 0,
        bottom: 0,
        right: 0,
        left: 0,
        backgroundColor: 'rgba(0,0,0,.3)',
    },
    imageHeaderContent: {
        position: 'relative',
        padding: theme.spacing(3),
        [theme.breakpoints.up('md')]: {
            padding: theme.spacing(6),
            paddingRight: 0,
        },
    },
}));

export default function ImageHeader(props) {
    const classes = useStyles();
    const {content} = props;

    return (
        <Paper className={classes.imageHeader} style={{backgroundImage: `url(${content.image})`}}>
            {<img style={{display: 'none'}} src={content.image} alt={content.imageText}/>}
            <div className={classes.overlay}/>
            <Grid container>
                <Grid item md={6}>
                    <div className={classes.imageHeaderContent}>
                        <Typography component="h1" variant="h3" color="inherit" gutterBottom>
                            {content.title}
                        </Typography>
                        <Typography variant="h5" color="inherit" paragraph>
                            {content.description}
                        </Typography>
                    </div>
                </Grid>
            </Grid>
        </Paper>
    );
}

ImageHeader.propTypes = {
    content: PropTypes.object,
};



